/*

https://www.redblobgames.com/grids/hexagons/

      3 -- 4
     /      \                 x - red, y - green, z - blue
    2        5                 +x
     \      /                   |
      1 -- 0               -z ---- +z
                                |
         B                     -x
       3 -- 4
    A /      \ C
     2        5
    F \      / D
       1 -- 0
         E

           ______
          /      \
         /  1.0   \______
         \        /      \
          \______/  1.1   \
          /      \        /
         /  0.0   \______/
         \        /      \
          \______/  0.1   \
          ^      \        /
         0.0.0    \______/

*/
export default (size, depth = 1, face) => {
    // Size
    const s = size;
    // 1/2 size
    const s12 = size / 2;
    // Width
    const w = s * Math.sqrt(3);
    // 1/2 width
    const w12 = w / 2;
    // Depth
    const d = depth;
    const faces = [
        // BOTTOM =================================================================
        {
            side: 'bt',
            dir: [0, -1, 0],
            corners: [
                [-s12, 0, -w12],
                [s12, 0, -w12],
                [-s, 0, 0],
                [s, 0, 0],
            ]
        },
        {
            side: 'bb',
            dir: [0, -1, 0],
            corners: [
                [-s12, 0, w12],
                [-s, 0, 0],
                [s12, 0, w12],
                [s, 0, 0],
            ],
        },
        // TOP ========================================================
        {
            side: 'tt',
            dir: [0, 1, 0],
            corners: [
                [-s12, d, -w12],
                [-s, d, 0],
                [s12, d, -w12],
                [s, d, 0],
            ]
        },
        {
            side: 'tb',
            dir: [0, 1, 0],
            corners: [
                [-s12, d, w12],
                [s12, d, w12],
                [-s, d, 0],
                [s, d, 0],
            ],
        },
        // LATERAL SIDES ===============================================
        {
            side: 'a',
            dir: [-1, 0, 0],
            corners: [
                [-s12, 0, -w12],
                [-s, 0, 0],
                [-s12, d, -w12],
                [-s, d, 0],
            ],
        },
        {
            side: 'b',
            dir: [0, 0, 1],
            corners: [
                [-s12, 0, -w12],
                [-s12, d, -w12],
                [s12, 0, -w12],
                [s12, d, -w12],
            ],
        },
        {
            side: 'c',
            dir: [1, 0, 0],
            corners: [
                [s12, 0, -w12],
                [s12, d, -w12],
                [s, 0, 0],
                [s, d, 0],
            ],
        },
        {
            side: 'd',
            dir: [1, 0, -1],
            corners: [
                [s, 0, 0],
                [s, d, 0],
                [s12, 0, w12],
                [s12, d, w12],
            ],
        },
        {
            side: 'e',
            dir: [0, 0, -1],
            corners: [
                [-s12, 0, w12],
                [s12, 0, w12],
                [-s12, d, w12],
                [s12, d, w12],
            ],
        },
        {
            side: 'f',
            dir: [-1, 0, -1],
            corners: [
                [-s, 0, 0],
                [-s12, 0, w12],
                [-s, d, 0],
                [-s12, d, w12],
            ],
        },
    ];
    switch (face) {
        case 'top':
            return faces.filter(face => {
                return ['tt', 'tb'].includes(face.side);
            });
        default:
            return faces;
    }
};
