import { FloatType, TextureLoader, EventDispatcher } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader';
export default class Resources extends EventDispatcher {
    constructor(sources) {
        super();
        this.sources = sources;
        this.items = {};
        this.toLoad = this.sources.length;
        this.loaded = 0;
        this.setLoaders();
        this.startLoading();
    }
    setLoaders() {
        this.loaders = {
            textureLoader: new TextureLoader(),
            RGBELoader: new RGBELoader(),
            gltfLoader: new GLTFLoader()
        };
        this.loaders.RGBELoader.setDataType(FloatType);
        // this.loaders.cubeTextureLoader = new THREE.CubeTextureLoader()
    }
    startLoading() {
        for (const source of this.sources) {
            switch (source.type) {
                case 'texture':
                    this.loaders.textureLoader.load(source.path, (file) => {
                        this.sourceLoaded(source.name, file);
                    });
                    break;
                case 'envMapTexture':
                    this.loaders.RGBELoader.load(source.path, (file) => {
                        this.sourceLoaded(source.name, file);
                    });
                    break;
                case 'glb':
                    this.loaders.gltfLoader.load(source.path, (file) => {
                        this.sourceLoaded(source.name, file);
                    });
                    break;
            }
        }
    }
    sourceLoaded(sourceName, file) {
        this.items[sourceName] = file;
        this.loaded++;
        if (this.loaded === this.toLoad) {
            this.dispatchEvent({ type: 'ready' });
        }
    }
}
