import { Color, MeshStandardMaterial, Mesh, PlaneBufferGeometry, RepeatWrapping, } from 'three';
import Main from '../Main';
export default class Sea {
    constructor() {
        this.main = new Main();
        this.scene = this.main.scene;
        this.resources = this.main.resources;
        this.debug = this.main.debug;
        this.map = this.main.map;
        this.setGeometry();
        this.setTextures();
        this.setMaterial();
        this.setMesh();
        if (this.debug.active) {
            this.setDebug();
        }
    }
    setGeometry() {
        this.geometry = new PlaneBufferGeometry(this.map.sea.size.width, this.map.sea.size.height, this.map.sea.segmentsQty.width, this.map.sea.segmentsQty.height);
    }
    setTextures() {
        this.textures = {};
        this.textures.map = this.resources.items.woodBaseColorTexture;
        this.textures.map.wrapS = this.textures.map.wrapT = RepeatWrapping;
        this.textures.map.repeat.set(48, 48);
        this.textures.normalMap = this.resources.items.woodNormalTexture;
        this.textures.normalMap.wrapS = this.textures.normalMap.wrapT = RepeatWrapping;
        this.textures.normalMap.repeat.set(48, 48);
        this.textures.roughnessMap = this.resources.items.woodRougMapTexture;
        this.textures.roughnessMap.wrapS = this.textures.roughnessMap.wrapT = RepeatWrapping;
        this.textures.roughnessMap.repeat.set(48, 48);
        this.textures.displacementMap = this.resources.items.woodHightMapTexture;
        this.textures.displacementMap.wrapS = this.textures.displacementMap.wrapT = RepeatWrapping;
        this.textures.displacementMap.repeat.set(48, 48);
    }
    setMaterial() {
        this.material = new MeshStandardMaterial();
        this.material.map = this.textures.map;
        this.material.normalMap = this.textures.normalMap;
        this.material.roughnessMap = this.textures.roughnessMap;
        this.material.displacementMap = this.textures.displacementMap;
        this.material.displacementScale = this.map.sea.material.displacementScale;
        this.material.roughness = this.map.sea.material.roughness;
        this.material.color = new Color(this.map.sea.color);
    }
    setMesh() {
        this.mesh = new Mesh(this.geometry, this.material);
        this.mesh.rotation.x = -Math.PI * .5;
        this.mesh.receiveShadow = true;
        this.scene.add(this.mesh);
    }
    // setMesh() {
    //   let xDist = 79
    //   let zDist = 99
    //   let xOff = -160
    //   let zOff = -150
    //     for(var i = 0; i < 5; i++){
    //       for(var j = 0; j < 5; j++){
    //           this.mesh  = new Mesh(this.geometry, this.material);
    //           this.mesh.position.x = xDist * i + xOff;
    //           this.mesh.position.z = zDist * j + zOff;
    //           this.mesh.rotation.x = -Math.PI * .5;
    //           this.mesh.receiveShadow = true;
    //           this.scene.add(this.mesh);
    //       }
    //   };
    //   }
    setDebug() {
        // tslint:disable-next-line:no-non-null-assertion
        this.debugFolder = this.debug.ui.addFolder('Sea');
        const params = {
            color: this.map.sea.color
        };
        // tslint:disable-next-line:no-non-null-assertion
        this.debugFolder
            .addColor(params, 'color')
            .name('Material Color')
            .onChange(() => {
            this.material.color = new Color(params.color);
        });
        // tslint:disable-next-line:no-non-null-assertion
        this.debugFolder
            .add(this.material, 'displacementScale')
            .name('Material Displacement Scale')
            .min(-2)
            .max(2)
            .step(.001);
        // tslint:disable-next-line:no-non-null-assertion
        this.debugFolder
            .add(this.material, 'roughness')
            .name('Material Roughness')
            .min(0)
            .max(1)
            .step(.001);
        // tslint:disable-next-line:no-non-null-assertion
        this.debugFolder
            .add(this.material, 'wireframe');
    }
}
