import { Scene, Color } from 'three';
import Debug from './Utils/Debug';
import Sizes from './Utils/Sizes';
import Time from './Utils/Time';
import Camera from './Camera';
import Renderer from './Renderer';
import World from './World/World';
import Resources from './Utils/Resources';
import Raycaster from './Utils/Raycaster';
import Controls from './Controls';
import DefaultConfig from './Configs/DefaultConfig';
import FirstMapSources from './Maps/FirstMap/Sources';
import FirstMap from './Maps/FirstMap/FirstMap';
const Config = DefaultConfig;
const Map = FirstMap;
const Sources = FirstMapSources;
let GameInstance = null;
export default class Main {
    constructor(_canvas) {
        if (GameInstance) {
            return GameInstance;
        }
        console.log(this);
        GameInstance = this;
        // tslint:disable-next-line:no-non-null-assertion
        this.canvas = _canvas;
        this.config = Config;
        this.map = Map;
        this.setScene();
        this.debug = new Debug();
        this.sizes = new Sizes();
        this.time = new Time();
        this.resources = new Resources(Sources);
        this.camera = new Camera();
        this.renderer = new Renderer();
        this.setEvents();
    }
    setScene() {
        this.scene = new Scene();
        this.scene.background = new Color('#FFEECC');
    }
    setEvents() {
        this.resources.addEventListener('ready', () => {
            this.onResourcesLoaded();
        });
        this.sizes.addEventListener('resize', () => {
            this.onResize();
        });
        this.time.addEventListener('tick', () => {
            this.onTick();
        });
    }
    onResourcesLoaded() {
        this.world = new World();
        this.controls = new Controls();
        this.raycaster = new Raycaster();
        if (this.debug.active) {
            this.debug.run();
        }
    }
    onResize() {
        this.camera.resize();
        this.renderer.resize();
    }
    onTick() {
        this.world?.update();
        this.raycaster?.update();
        this.controls?.update();
        this.camera.update();
        this.renderer.update();
    }
}
